import React from 'react';
import { Link } from 'gatsby-link';
import Helmet from 'react-helmet';
import { Container, Table } from 'semantic-ui-react';
import getText from '../utils/getText.js';

export default class Index extends React.Component {
  getTableRows = () => {
    const tableRows = [];
    const procedures = getText('prices.procedures');

    for (var i = 0; i < procedures.length; i++) {
      tableRows.push(<Table.Row>
                       <Table.Cell>
                         { procedures[i].name }
                       </Table.Cell>
                       <Table.Cell>
                         { procedures[i].price }
                       </Table.Cell>
                     </Table.Row>);
    }

    return tableRows;
  };

  render() {
    return <div id='prices'>
             <Container>
               <Table basic>
                 <Table.Header>
                   <Table.Row>
                     <Table.HeaderCell>
                       { getText('prices.header.procedure') }
                     </Table.HeaderCell>
                     <Table.HeaderCell>
                       { getText('prices.header.price') }
                     </Table.HeaderCell>
                   </Table.Row>
                 </Table.Header>
                 <Table.Body>
                   { this.getTableRows() }
                 </Table.Body>
               </Table>
             </Container>;
           </div>
  }
}



